import wx from 'weixin-js-sdk'
import ajax from './ajax'
import { getUrlQuery, session, storage } from './utils'
import Cookies from 'js-cookie'

let wechat = {
    // appId: 'wxc74dc9f02674b0a7', // 
    appId: 'wx039f3ef5e9976eb1',  // 
    openid: storage.get('openid'),
    configParam: {
        debug: false,
        timestamp: '',
        nonceStr: '',
        signature: ''
    },
    authCode: getUrlQuery('code'),
    scanQrCode (cb) {
        // console.log(this.configParam)
        wx.scanQRCode({
            desc: 'scanQRCode desc',
            needResult: 1, // 01
            scanType: ['qrCode', 'barCode'], // 
            success (res) {
                let result = res.resultStr
                cb && cb(result)
            },
            error (res) {
                if (res.errMsg.indexOf('function_not_exist') > 0) {
                    alert('')
                }
            }
        })
    },
    chooseImage (cb) {
        wx.chooseImage({
            count: 1, // 9
            sizeType: ['original', 'compressed'], // 
            sourceType: ['album', 'camera'], // 
            success: function (res) {
                let localIds = res.localIds   // IDlocalIdimgsrc
                wx.uploadImage({
                    localId: localIds, // IDchooseImage
                    isShowProgressTips: 1, // 1
                    success: function (res) {
                        let serverId = res.serverId   // ID
                        cb && cb(serverId)
                    }
                })
            }
        })
    },
    getConfigParam (cb) {
        ajax({
            url: '/common/weChat/getweChatInfo',
            success: (data) => {
                this.configParam.timestamp = data.timestamp
                this.configParam.nonceStr = data.nonceStr
                this.configParam.signature = data.signature
                cb()
            }
        })
    },
    setWxConfig ({jsApiList, ready, error}) {
        let set = () => {
            wx.config({
                debug: this.configParam.debug, // ,apialertpclogpc
                appId: this.appId, // 
                timestamp: this.configParam.timestamp, // 
                nonceStr: this.configParam.nonceStr, // 
                signature: this.configParam.signature, // 1
                // jsApiList: ['chooseWXPay'] // JSJS2
                jsApiList: jsApiList
            })
            ready && wx.ready(ready)
            error && wx.fail(error)
        }
        if (this.configParam.timestamp) {
            set()
        } else {
            this.getConfigParam(set)
        }
    },
    /**
     * code
     * @param clearQuery urlcodestate
     * @returns {String} code || ''
     */
    getCode (clearQuery = true) {
        if (clearQuery) {
            if (getUrlQuery('state') === 'wechat') { // codesessioncodestate
                session.set('wechat_code', getUrlQuery('code'))
                let url = window.location.href
                window.location.href = url.substring(0, url.indexOf('?'))
            } else {    // sessioncode
                let code = session.get('wechat_code')
                if (code) {
                    session.remove('wechat_code')
                    return code
                }
            }
        } else {
            if (getUrlQuery('state') === 'wechat') {
                return getUrlQuery('code')
            }
        }
        return ''
    },
    /**
     * 
     *  url  reUrlreUrlcode
     * reUrl
     * @param reUrl
     */
    wechatAuth (reUrl) {
        if (session.get('wechat_code')) return
        let redirectUri = encodeURIComponent(reUrl || window.location.href)
        // let currentUrl = encodeURIComponent('http://16535j5e29.51mypc.cn/anlianExam/mobile/index.html#/signUp');
        window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + this.appId +
            '&redirect_uri=' + redirectUri +
            '&response_type=code&scope=snsapi_userinfo' +
            '&state=' + 'wechat' +
            '#wechat_redirect'
    },
    /**
     * openid
     * @param cb
     */
    getOpenid (cb) {
        if (this.openid) {
            if (!Cookies.get('openid')) {
                Cookies.set('openid', this.openid)
            }
            cb && cb()
            return
        }
        let code = this.getCode(true)
        if (!code) {
            this.wechatAuth()
            return
        }
        ajax({
            url: '/common/weChat/getOpenid',
            data: {
                code: code
            },
            success: (data) => {
                this.openid = data.openid
                storage.set('openid', data.openid)
                Cookies.set('openid', data.openid)
                cb && cb()
            },
            error: (e) => {
                alert(e.message)
            }
        })
    },
}
export default wechat



