import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'

Vue.use(Vuex)
const debug = process.env.NODE_ENV === 'development'

const store = new Vuex.Store({
    state: {
        selectedInfo: {    // 
            daySltActive: 0, // 
            ticketType: 2, // 1  2
            selectedSpec: -1, // 
            selectedTime: -1, // 
            contactName: '',  // 
            contactPhone: '',  // 
            sex: 1,  // 1  2
            area: '',  // 
            areaCode: '', // 
            singleCount: 1, // 
        },
        scanStatus: 300, // 
        showMessage: false, // 
        visitNotice: '', // 
        pullupshow:false //
    },
    mutations,
    actions,
    strict: debug
})
export default store



